.MyModal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.5);
}

.MyModal__content{
    padding: 15px 20px;
    background-color: white;
    border-radius: 16px;
    min-width: 250px;
}

.active {
    display: flex;
    justify-content: center;
    align-items: center;
}